<template>
  <div class="page chat-page">
    <w-header>
      <div class="title">
        <router-link to="/">
          <button>
            <div>
              <i class="fas fa-angle-left fa-2x"></i>
            </div>
          </button>
        </router-link>
        <h3>{{ thePartner.name }}</h3>
        <span
          >({{ thePartner.details.gender }}, {{ thePartner.age }},
          {{ thePartner.details.country }})</span
        >
      </div>
    </w-header>
    <div id="outputScreen"></div>
    <div class="inputting">
      <textarea
        id="messageInput"
        autocomplete="off"
        @keydown.enter="sendMessage"
      ></textarea>
      <button id="sendBtn" @click="sendMessage">
        <i class="fas fa-angle-right fa-2x"></i>
      </button>
    </div>
    <transition name="opacity">
      <w-popup ref="popup" :message="message" />
    </transition>
  </div>
</template>

<script>
import WHeader from "../components/WHeader.vue";
import WPopup from "../components/WPopup.vue";

export default {
  name: "Chat",
  data() {
    return {
      message: "",
      resolvePromise: "",
      rejectPromise: "",
      partner: false,
    };
  },
  components: { WHeader, WPopup },
  computed: {
    socket() {
      return this.$store.state.socket;
    },
    thisUser() {
      return this.$store.state.user;
    },
    thePartner() {
      return this.$store.state.partner;
    },
    theEventsPreventingCounter() {
      return this.$store.state.chatPageVisits;
    },
  },
  methods: {
    sendMessage() {
      if (document.getElementById("messageInput").value.trim() === "") return;
      this.socket.emit("messageFromClient", {
        id: this.thisUser.id,
        name: this.thisUser.name,
        room: this.thisUser.room,
        message: document.getElementById("messageInput").value,
      });
      this.appendMessage(document.getElementById("messageInput").value, "me");
      document.getElementById("messageInput").value = "";
      document.getElementById("messageInput").focus();
    },
    appendMessage(message, byWho) {
      const messageBubble = document.createElement("div");
      const div = document.createElement("div");
      messageBubble.classList.add("message");

      if ((byWho, byWho === "me")) {
        div.classList.add("by-me");
      }

      messageBubble.textContent = `${message}`;

      div.appendChild(messageBubble);
      document.getElementById("outputScreen").appendChild(div);
    },

    async showPopup(options, next) {
      const confirmationResult = await this.$refs.popup.show(options);
      if (confirmationResult) {
        next();
      } else {
        next(false);
      }
    },
  },
  mounted() {
    if (!this.thePartner.id) {
      document.getElementById("home-link").click();
    }
    // ensure that the messaging board has no content in it :
    document.getElementById("outputScreen").innerHTML = "";

    // append messages coming from server :
    this.socket.on("messageFromServer", (data) => {
      this.appendMessage(data.message);

      document
        .getElementById("outputScreen")
        .scrollBy(
          0,
          document.getElementById("outputScreen").scrollHeight + 800
        );
    });

    // remove the partner object from store when he leaves the chat :
    this.socket.on("removePartner", (partnerId) => {
      if (this.thePartner.id === partnerId) {
        this.partnerLeft = true;
        document.getElementById("home-link").click();
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    if (!this.partnerLeft) {
      this.showPopup(
        {
          message:
            "Leaving this screen will lead to lose the conversation forever !",
          isCancel: true,
        },
        next
      );
    } else {
      this.showPopup(
        {
          message: `${this.thePartner.name} left the chat`,
          isCancel: false,
        },
        next
      );
    }
  },
  beforeUnmount() {
    this.socket.emit("leaveRoom", {
      roomId: this.thisUser.room,
      isPartner: true,
    });
  },
  unmounted() {
    this.socket.removeAllListeners("messageFromServer");
    this.socket.removeAllListeners("removePartner");
  },
};
</script>

